@use 'sass:map';
@use 'sass:color';
@use 'variables' as *;

@mixin respond($size) {
  @if map.has-key($breakpoints, $size) {
    @media (max-width: map.get($breakpoints, $size)) {
      @content;
    }
  }
}

@mixin skeleton-gradient($color-scheme) {
  &::after {
    background-image: linear-gradient(
      90deg,
      color.adjust($color-scheme, $alpha: -1) 0%,
      color.adjust($color-scheme, $alpha: -0.8) 20%,
      color.adjust($color-scheme, $alpha: -0.5) 60%,
      color.adjust($color-scheme, $alpha: -1)
    );
  }
}

@mixin adjust-color($theme-color) {
  background: color.adjust($theme-color, $alpha: -0.7);
}

@mixin hover-supported {
  @media (hover: hover) {
    @content;
  }
}

.fetchingDataSpinner {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 4px;
  right: 4px;
  top: 4px;
  width: calc(100% - 20px);
  height: 100%;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
}
